import { Store } from '../store';

export const loadDetentions = () =>
    fetch('/detentions.json', { headers: { 'cache-control': 'no-cache', pragma: 'no-cache' } })
        .then((r) => r.json())
        .then(({ detentions, version, since }) => {
            const parsedVersion = version || 'konnte nicht geladen werden';
            const parsedSince = since || 'konnte nicht geladen werden';
            const parsedDetentions = Array.isArray(detentions)
                ? detentions.map(({ name, min, detentions, finished }) => ({
                      name,
                      min: min || 0,
                      detentions: detentions || 0,
                      finished: finished || 0,
                  }))
                : [];
            Store.set({ detentionsData: { version: parsedVersion, detentions: parsedDetentions, since: parsedSince } });
        })
        .catch(() => {});
